import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap';
import { makeStyles } from '@material-ui/core/styles';
import Fab from '@material-ui/core/Fab';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import FitnessCenterIcon from '@material-ui/icons/FitnessCenter';
import DirectionsBikeIcon from '@material-ui/icons/DirectionsBike';
import SportsEsportsIcon from '@material-ui/icons/SportsEsports';
import SportsBasketballIcon from '@material-ui/icons/SportsBasketball';
import SportsGolfIcon from '@material-ui/icons/SportsGolf';
import SportsHandballIcon from '@material-ui/icons/SportsHandball';
import SportsVolleyballIcon from '@material-ui/icons/SportsVolleyball';
import SportsFootballIcon from '@material-ui/icons/SportsFootball';
import './buttons.scss';

const useStyles = makeStyles((theme) => ({
  fab: {
    margin: theme.spacing(1),
    width: '120px',
    height: '120px',
  },
}));

export default function ExampleModal() {
  const classes = useStyles();

  const [show, setShow] = useState({ show: false, text: '' });

  const handleClose = () => setShow({ ...show, show: false });
  const handleShow = (text) => setShow({ show: true, text });

  const buttons = [
    { text: 'Ftbol', icon: <SportsSoccerIcon /> },
    { text: 'Fitness', icon: <FitnessCenterIcon /> },
    { text: 'Ciclismo', icon: <DirectionsBikeIcon /> },
    { text: 'eSports', icon: <SportsEsportsIcon /> },
    { text: 'Baloncesto', icon: <SportsBasketballIcon /> },
    { text: 'Golf', icon: <SportsGolfIcon /> },
    { text: 'Balonmano', icon: <SportsHandballIcon /> },
    { text: 'Voleibol', icon: <SportsVolleyballIcon /> },
    { text: 'Ftbol americano', icon: <SportsFootballIcon /> },
  ];

  return (

    <>
    
      <Grid container spacing={3} className='GridContainer'>
        {buttons.map((button) => (
            <div className="buttonsContainer">
    <Fab
            variant="primary"
            onClick={() => handleShow(button.text)}
            color="primary"
            className={classes.fab}
          >
            {button.icon}
            <Typography variant="subtitle1"></Typography>
          </Fab>
          </div>
        ))}
      </Grid>

<div className="modalContainer">
      <Modal show={show.show} onHide={handleClose} >
        <Modal.Header closeButton>
          <Modal.Title>{show.text}</Modal.Title>
        </Modal.Header>
        <Modal.Body>{show.text}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Cerrar
          </Button>
        </Modal.Footer>
      </Modal>
      </div>
    </>
  );
}
