import React from "react";

import WhatsAppIcon from './wa-whatsapp-icon.svg';

import './whatsapp.scss'

const WhatsAppChatButton = () => {
  const phoneNum = "+5492236898861";
  const message = "Hola, cmo ests Leandro? Quiero comenzar un entrenamiento!";

  const handleClick = () => {
    window.open(`https://wa.me/${phoneNum}?text=${encodeURIComponent(message)}`, '_blank');
  };

  return (

    <button className="WhatsAppBtn" onClick={handleClick}>
              <img src={WhatsAppIcon} alt="WhatsApp" width="100" height="100" />

      </button>
  );
};

export default WhatsAppChatButton;