import React from "react";
import instagramIcon from './instagram-round-color-icon.svg';
import './instagram.scss';
const IgChatButton = () => {


  const handleClick = () => {
    window.open(`https://www.instagram.com/nahuelcaballerofitness/`);
  };

  return (
    <button className="instagram-logo" onClick={handleClick}>
        <img src={instagramIcon} alt="Instagram" width="100" height="100"/>
      </button>
  );
};

export default IgChatButton;